@font-face {
  font-family: 'nalux';
  src:  url('../fonts/nalux.eot');
  src:  url('../fonts/nalux.eot#iefix') format('embedded-opentype'),
    url('../fonts/nalux.ttf') format('truetype'),
    url('../fonts/nalux.woff') format('woff'),
    url('../fonts/nalux.svg#nalux') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'nalux' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-youtube:before {
  content: "\e900";
}
.icon-linkedin:before {
  content: "\e901";
}
.icon-facebook:before {
  content: "\e902";
}
.icon-arrow-expand:before {
  content: "\e903";
}
.icon-arrow-next:before {
  content: "\e904";
}
