header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

header:before {
  content: '';
  background: #10262c;
  background: linear-gradient(0deg, rgba(16, 38, 44, 0) 0%, #10262c 100%);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  transition: background 0.8s ease;
}

header.fixed:before {
  background: #10262c;
}

header .container {
  display: flex;
  align-items: center;
  height: 72px;
}

header .container nav {
  flex: 2;
}

