h6 {
  font-weight: 500;
  text-transform: capitalize;
  margin: 0;
  line-height: 1.4;
}

.container {
  max-width: 1280px;
  padding: 0 48px;
  margin: 0 auto;
}

html {
  overflow-x: hidden;
}

body {
  font-family: 'Maven Pro', sans-serif;
  padding: 0;
  margin: 0;
  background-color: #06262D;
  font-size: 16px;
  line-height: 1.3;
  color: #fff;
}

.heading-title {
  font-family: 'Fahkwang', sans-serif;
  font-size: 50px;
  color: #C2A97C;
  font-weight: 400;
  margin-bottom: 40px;
}

.page-title .heading-title {
  font-size: 70px;
  font-weight: 700;
}

h2.title {
  font-weight: 500;
  font-size: 36px;
}

h3.title {
  font-weight: 500;
  font-size: 24px;
}

.date {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
}
